.markdown {
    line-height: 28px;
    --path: none;
    --radius-top: 12px;
    --radius-bottom: 12px;
    --padding-top: 1rem;
    --padding-bottom: 1rem;
}

.markdown p {
    padding-bottom: 1rem;
}

.markdown a {
    border-bottom-width: 1px;
    border-color: var(--link);
    color: var(--link);
}

.markdown hr {
    padding-top: 2rem;
    opacity: 0.6;
}

.markdown hr:where([data-theme='dark'], [data-theme='dark'] *) {
    opacity: 0.1;
}

.markdown h2 {
    margin-top: 0.5rem;
    padding-bottom: 2rem;
    font-size: 1.875rem;
    line-height: 2.25rem;
    font-weight: 700;
}

.markdown h3 {
    margin-top: 0.5rem;
    padding-bottom: 2rem;
    font-size: 1.5rem;
    line-height: 2rem;
    font-weight: 700;
}

.markdown h4 {
    margin-top: 0.5rem;
    padding-bottom: 2rem;
    font-size: 1.25rem;
    line-height: 1.75rem;
    font-weight: 700;
}

.markdown :not(pre) > code {
    border-radius: 10px;
    background: var(--inlineCode-bg);
    color: var(--inlineCode-text);
    padding: 0.15em 0.2em 0.05em;
    white-space: normal;
}

.markdown pre {
    margin-left: -1rem;
    margin-right: -1rem;
    margin-bottom: 2rem;
    overflow-y: auto;
    padding: 1rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    clip-path: var(--path);
    border-top-right-radius: var(--radius-top);
    border-top-left-radius: var(--radius-top);
    border-bottom-right-radius: var(--radius-bottom);
    border-bottom-left-radius: var(--radius-bottom);
    padding-top: var(--padding-top);
    padding-bottom: var(--padding-bottom);
}

.markdown pre code {
    width: auto;
}

.markdown blockquote {
    position: relative;
    left: -0.5rem;
    margin-left: -1rem;
    margin-bottom: 2rem;
    padding-left: 1rem;
    font-style: italic;
    border-left: 3px solid hsla(0, 0%, 0%, 0.9);
    border-left-color: inherit;
    opacity: 0.8;
}

.markdown blockquote p {
    margin: 0;
    padding: 0;
}

.markdown p img {
    margin-bottom: 0;
}

.markdown ul {
    margin-top: 0;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    margin-bottom: 1.75rem;
    list-style-position: outside;
    list-style-image: none;
    list-style: disc;
}

.markdown li {
    margin-bottom: calc(1.75rem / 2);
}

.markdown img {
    margin-bottom: 2rem;
    max-width: 100%;
}

.markdown pre [data-highlighted-line] {
    margin-left: -16px;
    padding-left: 12px;
    border-left: 4px solid #ffa7c4;
    background-color: #022a4b;
    display: block;
    padding-right: 1em;
}
